<template>
<div class="custom-container">
  <div class="col-md-8">

    <div class="col-12 box" >
        <canvas id="myChart" style="height: 350px; width:90%;"></canvas>
    </div>
     
  </div>
  </div>            
</template>

<script>

import axios from 'axios';
import Loader from './Loader';
import Chart from 'chart.js';

export default {
  //props: ['quizData'],
  components:{
    Loader
  },
  data(){
      return{
          gamesList: "",
          game: "",
          result: [],
          timer: '',
          counter: '',
          showLoader: false,
          correct:0,
          inCorrect:0,
          notanswer:0,
          poll:0,
          isComplete: false,
          correctId: '',
          currentId: '',
          loggedInIds: [],
          activeQuestion: 1,
          pollAnswers: {}

      }
  },
  methods:{
       
    createChart: function(chartId, chartData){

        const ctx = document.getElementById(chartId);
        const myChart = new Chart(ctx, {
            type: chartData.type,
            data: chartData.data,
            options: chartData.options
        });
    },

    drawChart: function(){

        let chartData = {
            type: 'bar',
            data: {
                labels: ['A','B','C'],
                datasets: [
                    {
                        label: 'Poll',
                        backgroundColor: 'red',
                        data: [50,30,89],
                        borderColor: 'white',
                        hoverBackgroundColor: "rgba(255,99,132,0.4)",
                        hoverBorderColor: "rgba(255,99,132,1)",
                        borderWidth: 1
                    }, 
                    ]
            },
            options: {
           animation: {
				duration:50000,  
                easing: 'easeOutBounce'
        
},
           responsive: true,
                title: {
                    display: true,
                    text: 'Live Polling'
                },
                tooltips: {
                    mode: 'index',
                    intersect: true
                },
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero:true
                        }
                    }]
                }
            }
        }


        this.createChart('myChart', chartData);

    }
  },

  mounted(){

      this.drawChart();
       
  }
}

</script>

<style scoped>

    .custom-disable{
        pointer-events: none;
        opacity: 0.5;
    }
    
    .left-icon{
        position: absolute;
        left: 15px;
        top: calc(50% - 25px);
        font-size: 25px;
        cursor: pointer;
    }

    .right-icon{
        position: absolute;
        right: 15px;
        top: calc(50% - 25px);
        font-size: 25px;
        cursor: pointer;
    }

    .user{
        background: aliceblue;
        padding: 5px;
        border: 1px solid;
        margin: 3px;
    }

    .c-btn{
        border-radius: 0px;
        min-width: 100px;
        margin: 5px;
    }

    .progress{
        transition: all 1s cubic-bezier(1, 1, 1, 1);
        height: 5px;
        width: 0%;
        background: #079b80;
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        border-radius: 0;
    }
    .correct{
        background: green;
        color: #fff;
    }

    .incorrect{
        background: red;
        color: #fff;
    }

    .custom-container{
        display: flex;
        justify-content: center;
    }
    .box{
        /* width: 90%; */
        margin-top: 100px;
        min-height: 200px;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 15px 20px 35px;
        -webkit-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        -moz-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
    }

    .round{
        text-align: center;
        font-weight: 600;
    }

    .timer{
        margin: 10px;
        display: flex;
        justify-content: center;
    }

    .timer .count{
        border: 1px solid #333;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        line-height: 25px;
        text-align: center;
    }

    .center{
        display: flex;
        justify-content: center;
    }

    .options{
        display: flex;
        justify-content: space-around
    }

    .option{
        border: 1px solid #ccc;
        border-radius: 10px;
        min-height: 40px;
        /* line-height: 35px; */
        /* width: 45%; */
        margin: 10px;
        padding: 10px;
        cursor: pointer;
    }

    .question{
        font-weight: 600;
        margin-bottom: 30px;
        text-align: center;
    }
    #myChart{
      margin:10px;
    }
    template{
      margin: 70px;
    }
</style>



